html,body,h1,h2,h3,h4,div,p,ul,li,button{
  margin: 0;
  padding: 0;
}
html{
  width: 100vw;
  height: 100vh;
}
body{
  width: 100vw;
  height: 100vh;
  overflow: hidden;
  position: relative;
  min-width: 600px;
}
li{
  list-style: none;
}
a{
  color: black;
  text-decoration: none;
}